.top2 {
    position: relative;
    width: 100% !important;
    display: block;
    height: 400px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
    background-position-y: center;
    background-image: url("https://img4.thuthuatphanmem.vn/uploads/2020/12/26/hinh-nen-nhat-ban_125915822.jpg");
  }
  .content{
    margin-top: 40px;
    .content-product {
      margin: auto;
      max-width: 1350px;
      width: 75%;
      .content-img{
          width: 45%;
          img{
              width: 100%;
              height: 400px;
          }
          .item{
            display: flex;
            justify-content: space-around;
            img{
              width: 90px;
              height: 90px;
            }
          }
      }
    }
  }