html {
    margin: 0% !important;
    padding: 0% !important;
  }
  .container {
    width: 100% !important;
  }
  .top {
    position: relative;
    width: 100% !important;
    display: block;
    height: 800px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
    background-position-y: bottom;
    transition: 700ms ease-in;
    .content {
      width: 55%;
      margin: 0 auto;
      text-align: center;
      padding-top: 180px;
      .title {
        color: white;
        span {
          text-decoration-line: underline;
          text-decoration-color: blueviolet;
        }
      }
      h1 {
        font-size: 50px;
        font-family: cursive;
        animation: topToCentent ease-out 1.6s;
      }
      h2 {
        font-family: Arial, Helvetica, sans-serif;
        animation: topToCentent ease 1s;
      }
  
      .search {
        width: 350px;
        margin: auto;
        margin-top: 50px;
        border-radius: 30px;
        display: flex;
        background-color: white;
        border-radius: 30px !important;
        animation: option ease-in 2s;
        .sear {
          width: 80%;
          margin-left: 5%;
          border: none;
          color: rgb(150, 146, 146);
        }
        .btn {
          width: 35px;
          margin: 0;
          padding: 10px;
          background-color: white;
          .icon {
            width: 35px;
            color: rgb(163, 163, 163);
          }
        }
        .btn:hover > .icon {
          color: rgb(105, 103, 103);
        }
      }
      .options {
        width: 640px;
        margin: auto;
        margin-top: 30px;
        display: flex;
        animation: option ease 2000ms;
        .option {
          width: 130px;
          background: rgba(82, 65, 65, 0.4);
          margin: 20px;
          color: white;
          cursor: pointer;
          padding: 15px 0;
          img {
            width: 50%;
            padding: 10px;
          }
        }
      }
    }
  }
  .content-list {
    text-align: center;
    .title {
      width: 270px;
      text-align: center;
      margin: auto;
      margin-top: 50px;
      h5 {
        color: rgb(151, 148, 148);
      }
      h2 {
        font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
        font-size: 30px;
        margin-top: 5px;
        span {
          text-decoration-line: underline;
          text-decoration-color: #14b9d5;
        }
      }
    }
    .content-products {
      margin: auto;
      max-width: 1350px;
      .card {
        margin: auto;
        width: 90%;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
      }
    }
  }
  .introduct {
    background-image: url("https://img4.thuthuatphanmem.vn/uploads/2020/12/26/anh-nen-nhat-ban-hoang-hon_125910670.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 333px;
    background-position-x: center;
    background-position-y: center;
    margin-top: 30px;
    .title {
      padding-top: 100px;
      width: 70%;
      margin: auto;
      .title1 {
        color: white;
        font-size: 60px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        span {
          text-decoration-line: underline;
          text-decoration-color: rgb(219, 11, 11);
        }
      }
    }
  }
  .picture {
    max-width: 1350px;
    margin: auto;
    position: relative;
    margin-top: 40px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    .depiction {
      margin: auto;
      width: 450px;
      margin-bottom: 50px;
      text-align: left;
      height: 350px;
      p {
        color: rgb(110, 106, 106);
        font-size: 20px;
        font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
          "Lucida Sans", Arial, sans-serif;
      }
      .title1 {
        font-family: cursive;
      }
      .btn {
        .btn-bookRom {
          border-radius: 30px;
          background: rgb(137, 242, 98);
          background: linear-gradient(
            90deg,
            rgba(137, 242, 98, 1) 3%,
            rgba(74, 199, 27, 1) 43%,
            rgba(39, 217, 19, 0.9780287114845938) 70%,
            rgb(175, 235, 148) 100%
          );
          &:hover {
            background: rgb(74, 199, 27);
            background: linear-gradient(
              90deg,
              rgba(74, 199, 27, 1) 19%,
              rgba(115, 235, 71, 1) 28%,
              rgba(34, 196, 16, 0.9780287114845938) 66%,
              rgba(167, 242, 132, 1) 100%
            );
          }
        }
      }
    }
    .carousel {
      margin: auto;
      width: 700px;
      position: relative;
      text-align: center !important;
      // align-items: center;
      .header {
        position: absolute;
        z-index: 1;
        width: 100%;
        .date {
          width: 70%;
          margin: auto;
          background-color: #14b9d5;
          display: flex;
          justify-content: space-evenly;
          color: white;
          padding: 5px;
        }
      }
    }
  }
  
  @keyframes topToCentent {
    from {
      opacity: 0;
      transform: translateY(-140px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  @keyframes option {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @media screen and (max-width: 1150px) {
    .search {
      padding-left: 0 !important;
      width: 220px !important;
      margin-top: 50px;
      height: 50px;
      .sear {
        width: 80%;
        margin-left: 5%;
        border: none;
        height: 30px !important;
        margin: 3px;
      }
      .btn {
        width: 20px !important;
        height: 30px;
        margin: 7px 0px !important;
        .icon {
          width: 100% !important;
          color: rgb(163, 163, 163);
        }
      }
    }
    
  }
  